type IconType = "triangle" | "circle" | "arrowUp" | "upDown" | "box" | "hexa" | "dollar" | "euro" | "camera" | "controller" | "book" | "html5" | "css" | "python" | "unity" | "javascript" | "r" | "react" | "gatsby" | "tableau" | "weight" | "guitar" | "paperplane"

type SVGProps = {
  stroke?: boolean
  color?: string | number | any
  width: number
  icon: IconType
  left: string
  top: string
  hiddenMobile?: boolean
}

const SVG = ({ stroke = false, color = ``, width, icon, left, top, hiddenMobile = false }: SVGProps) => (
  <svg
    sx={{
      position: `absolute`,
      stroke: stroke ? `currentColor` : `none`,
      fill: stroke ? `none` : `currentColor`,
      display: hiddenMobile ? hidden : `block`,
      color,
      width,
      left,
      top,
    }}
    viewBox={icons[icon].viewBox}
  >
    {icons[icon].shape}
  </svg>
)

export default SVG